import { useEffect, useState } from 'react';
import { getMovieCastById } from 'service/api';
import { Grid, GridItem, Text } from 'components/App/App.styled';
import Loader from 'components/Loader/Loader';

function Cast({ id, btn }) {
  const [cast, setCast] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    setIsLoading(true);
    if (!id) return;

    getMovieCastById(id)
      .then(data => {
        if (data.cast.length === 0) return;
        setCast(data.cast);
      })
      .catch(error => {
        console.log(error);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }, [id]);
  return (
    <>
      {isLoading && <Loader />}

      <Grid style={{ display: btn ? 'none' : 'block' }}>
        {cast.map(({ id, profile_path, name }) => (
          <GridItem key={id}>
            <img
              src={
                profile_path
                  ? `https://image.tmdb.org/t/p/w500${profile_path}`
                  : 'data:image/png;base64,'
              }
              alt={id}
            />
            <Text>{name}</Text>
          </GridItem>
        ))}
      </Grid>
    </>
  );
}
export default Cast;
