import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { getMovieById } from 'service/api';
import { Container } from 'components/App/App.styled';
import { useLocation } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { Section } from './../components/App/App.styled';
import Loader from 'components/Loader/Loader';
import MovieInfo from 'components/MovieInfo/MovieInfo';

const MovieDetails = () => {
  const { movieId } = useParams();
  const [movie, setMovie] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [genres, setGenres] = useState([]);
  const [img, setImg] = useState(
    'data:image/png;base64,'
  );
  const location = useLocation();
  const goBackLink = location?.state?.from ?? '/';

  useEffect(() => {
    setIsLoading(true);
    getMovieById(movieId)
      .then(data => {
        setMovie(data);
        if (data.genres.lenght === 0) return;
        setGenres(data.genres?.map(g => g.name).join(', '));
        if (data.poster_path === undefined) return;
        setImg(`https://image.tmdb.org/t/p/w500${data.poster_path}`);
      })
      .catch(error => {
        console.log(error);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }, [movieId]);

  return (
    <Section>
      <Container>
        {isLoading ? (
          <Loader />
        ) : (
          <>
            <div
              style={{
                marginBottom: '60px',
                color: '#f2f2f2',
                letterSpacing: '0.06em',
                textDecoration: 'underline',
                borderColor: 'gray',
              }}
            >
              <Link to={goBackLink}>Back to Popular Movies</Link>
            </div>
            <MovieInfo
              img={img}
              id={movie.id}
              title={movie.title}
              score={movie.vote_average}
              overview={movie.overview}
              genres={genres}
            />
          </>
        )}
      </Container>
    </Section>
  );
};
export default MovieDetails;
